___CSS_LOADER_EXPORT___.push([module.id, `.sgroup-container {
    position: relative;
    min-height: 600px;
    margin-top: 40px;
}

.sgroup {
    position: absolute;
    z-index: 0;
    width: 50%;
    height: 50%;
    overflow: hidden;
    cursor: pointer;
    transform: scale(1);
    transition: all 500ms cubic-bezier(0.4, 0, 0.2, 1);
    background-size: cover;
    background-position: 50% 50%;
}

.sgroup:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
    transition: background-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.sgroup:hover:after {
    background-color: rgba(0,0,0,0.2);
}

.sgroup:nth-child(1) {
    top: 0;
    left: 0;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.sgroup:nth-child(2) {
    top: 0;
    left: 50%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.sgroup:nth-child(3) {
    top: 50%;
    left: 0;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.sgroup:nth-child(4) {
    top: 50%;
    left: 50%;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.sgroup.is-expanded {
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    cursor: initial;
}

.has-expanded-item .sgroup:not(.is-expanded) {
    transform: scale(0);
}

.close-sgroup {
    z-index: 1;
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    line-height: 1;
    font-size: 50px;
    text-align: center;
    color: var(--light-blue);
    opacity: 0;
    cursor: pointer;
    pointer-events: none;
    transition: opacity 150ms linear;
    will-change: opacity;
}

.sgroup.is-expanded .close-sgroup {
    opacity: 1;
    transition-delay: 500ms;
    pointer-events: initial;
}

.title-box {
    z-index: 1;
    position: relative;
    display: flex;
    padding: 0 20px;
    height: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: var(--light-blue);
    text-shadow: -1px 0 1px #989898, 0 1px 1px #989898, 5px 5px 10px rgba(0, 0, 0, 0.4), -5px -5px 10px rgba(0, 0, 0, 0.4);
    font-size: 26px;
    font-weight: 600;
}

.sgroup.is-expanded:after {
    background-color: rgba(0,0,0,0.7);
}

.sgroup.is-expanded .title-box {
    justify-content: flex-start;
    height: auto;
    margin: 30px 40px;
    font-size: 26px;
}

.sgroup.is-expanded .info-box {
    z-index: 1;
    position: relative;
    display: flex;
    padding: 0 20px;
    height: 75%;
    overflow: hidden;
    align-items: center;
}

.info-col-1 {
    padding: 0 40px;
    width: 40%;
}

.info-col-1 img {
    width: 100%;
}

.info-col-2 {
    width: 60%;
    max-height: 100%;
    overflow-y: auto;
    font-size: 22px;
    color: var(--light-blue);
    line-height: 1.6;
    opacity: 0;
    transition: opacity 0.5s linear 0.4s;
    padding: 0 40px;
}

.is-expanded .info-box .info-col-2 {
    opacity: 1;
}

.sgroup-container-mobile {
    color: var(--light-blue);
    font-size: 22px;
    min-height: 640px;
    display: none;
}

.sgroup-mobile-item1 {
    background-color: var(--green);
}

.sgroup-mobile-item2 {
    background-color: var(--pink);
}

.sgroup-mobile-item3 {
    background-color: var(--blue);
}

.sgroup-mobile-item4 {
    background-color: var(--dark-green);
}

.sgroup-mobile-wrapper {
    font-weight: 400;
    font-size: 24px;
    padding-bottom: 20px;
}

.page_text_bottom {
    padding: 80px 20px 40px 20px;
    font-size: 22px;
    color: var(--dark-green);
    text-align: justify;
}

.bottom_button {
    position: relative;
    min-height: 50px;
    width: 100%;
    background-color: var(--green);
    border-radius: var(--border-radius);
    transition: all 0.9s;
    margin-top: 40px;
}

.section-infrastructure {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100vh;
    transition: transform 0.5s ease;
}

